import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Category from './panels/Category'
import AboutItem from "./panels/AboutItem"

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(null); //<ScreenSpinner size='large' />
	const [targetCategory, setTargetCategory] = useState(null);
	const [activeItem, setActiveItem] = useState(null);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = panelName => {
		setActivePanel(panelName);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} setTargetCategory={setTargetCategory}/>
			<Category id='category' fetchedUser={fetchedUser} go={go} targetCategory={targetCategory} setActiveItem={setActiveItem}/>
			<AboutItem id='aboutItem' fetchedUser={fetchedUser} go={go} targetCategory={targetCategory} activeItem={activeItem}/>
		</View>
	);
}

export default App;

