import React, {useState, useEffect} from 'react';
import {
    Gallery,
    Header,
    Placeholder,
    Banner,
    Panel,
    PanelHeader,
    Card,
    CardGrid,
    PanelHeaderBack,
    Button, MiniInfoCell, Caption, Subhead, TabsItem, Tabs, Div, HorizontalScroll, Link, Group, Footer, Spinner
} from "@vkontakte/vkui";
import './Home.css'
import Icon16Chevron from '@vkontakte/icons/dist/16/chevron';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon24Phone from "@vkontakte/icons/dist/24/phone";
import Icon24LogoVk from "@vkontakte/icons/dist/24/logo_vk";
import Icon24Linked from "@vkontakte/icons/dist/24/linked";

const Category = ({ id, go, fetchedUser, targetCategory, setActiveItem }) => {
    const [subcat, setSubcat] = useState(targetCategory.subcat);
    const [activeSubcat, setActiveSubcat] = useState(null);
    const [arrItems, setArrItems] = useState([])
    const [filtredArrItem, setFiltredArrItem] = useState(null)
    const [isLoad, setIsLoad] = useState(<Spinner/>)

    function filter(subcat){
        const newArr = arrItems.filter(item=>item.subcat.replace(/\s+/g, ' ').trim() === subcat.replace(/\s+/g, ' ').trim())
        setFiltredArrItem(newArr)
    }

    useEffect(() => {
        isLoad === false && setIsLoad(<Spinner/>)
        fetch('https://sahpossum.herokuapp.com/items/getItems', {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify({
                "cat": targetCategory.title,
                "subcat": activeSubcat !== null ? activeSubcat : ''
            }), // тип данны
        })
            .then(res=>res.json())
            .then(res=>{
                const arr = res.map(item=>{
                    const img = item.IP_PROP13.replace('export_file_mongo_files', 'https://zoomagasin.ru/upload')
                    return {
                        id: item._id,
                        name: item.IE_NAME,
                        description: item.IE_DETAIL_TEXT,
                        img: img,
                        cat: item.IC_GROUP0,
                        subcat: item.IC_GROUP1,
                        price: item.CV_PRICE_1
                    }
                })
                setArrItems(arr)
                setIsLoad(false)
            })
            .catch(e=>console.log(e))
    }, []);

    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
            <Placeholder
                icon={<div style={{
                    background: 'url('+targetCategory.icon+') no-repeat',
                    height: 50,
                    width: 50
                }}/>}
            >
                Выбрана категория: {targetCategory.title}
            </Placeholder>
            {
                targetCategory.subcat &&
                <Group header={<Header mode="secondary">Выберите тип товара</Header>}>
                    <HorizontalScroll>
                        <Tabs>
                            {
                                targetCategory.subcat.map(subcat=>{
                                    return (
                                        <TabsItem
                                            onClick={() => {
                                                if(activeSubcat === subcat){
                                                    setActiveSubcat(null)
                                                    setFiltredArrItem(null)
                                                } else {
                                                    setActiveSubcat(subcat)
                                                    filter(subcat)
                                                }
                                            }}
                                            selected={activeSubcat === subcat}
                                        >
                                            {subcat}
                                        </TabsItem>
                                    )
                                })
                            }
                        </Tabs>
                    </HorizontalScroll>
                </Group>
            }
            {
                activeSubcat !== null &&
                <MiniInfoCell textWrap={'full'}>Ниже будут выводиться карточки с - {activeSubcat}</MiniInfoCell>
            }
            <Div>
                {
                    isLoad ? isLoad :
                        <React.Fragment>
                            {
                                filtredArrItem === null ?
                                    <CardGrid>
                                        {
                                            arrItems.map(item=>{
                                                return(
                                                    <Card
                                                        onClick={()=>{
                                                            setActiveItem(item)
                                                            go('aboutItem')
                                                        }}
                                                        style={{padding: 2, borderRadius: 13, margin: 0, backgroundColor: '#007151'}}
                                                        size="m"
                                                        mode="shadow"
                                                    >
                                                        <div style={{
                                                            height: 150,
                                                            backgroundImage: 'url(' + item.img + ')',
                                                            backgroundSize: 'cover',
                                                            backgroundPosition: 'center 35%',
                                                            backgroundRepeat: 'no-repeat',
                                                            borderRadius: 13
                                                        }}>
                                                        </div>
                                                        <MiniInfoCell textWrap={'full'}><Caption level={1}>{item.name}</Caption></MiniInfoCell>
                                                        <MiniInfoCell before={<Icon24MoneyCircle height={20} width={20}/>}><Subhead>{item.price} руб.</Subhead></MiniInfoCell>
                                                        <MiniInfoCell><Button size="m" stretched mode="outline">В корзину</Button></MiniInfoCell>
                                                    </Card>
                                                )
                                            })
                                        }
                                    </CardGrid> :
                                    <CardGrid>
                                        {
                                            filtredArrItem.map(item=>{
                                                return(
                                                    <Card
                                                        onClick={()=>{
                                                            setActiveItem(item)
                                                            go('aboutItem')
                                                        }}
                                                        style={{padding: 2, borderRadius: 13, margin: 0, backgroundColor: '#007151'}}
                                                        size="m"
                                                        mode="shadow"
                                                    >
                                                        <div style={{
                                                            height: 150,
                                                            backgroundImage: 'url(' + item.img + ')',
                                                            backgroundSize: 'cover',
                                                            backgroundPosition: 'center 35%',
                                                            backgroundRepeat: 'no-repeat',
                                                            borderRadius: 13
                                                        }}>
                                                        </div>
                                                        <MiniInfoCell textWrap={'full'}><Caption level={1}>{item.name}</Caption></MiniInfoCell>
                                                        <MiniInfoCell before={<Icon24MoneyCircle height={20} width={20}/>}><Subhead>{item.price} руб.</Subhead></MiniInfoCell>
                                                        <MiniInfoCell><Button size="m" stretched mode="outline">В корзину</Button></MiniInfoCell>
                                                    </Card>
                                                )
                                            })
                                        }
                                    </CardGrid>
                            }
                        </React.Fragment>
                }
            </Div>
            <Group header={<Header mode="secondary">Есть вопросы?</Header>}>
                <MiniInfoCell before={<Icon24Phone height={20} width={20}/>}><Link href="tel:+79022954808" target="_blank">+7 (902) 294-48-08</Link></MiniInfoCell>
                <MiniInfoCell before={<Icon24LogoVk height={20} width={20}/>}><Link href="https://vk.com/zoomagasin" target="_blank">vk.com/zoomagasin</Link></MiniInfoCell>
                <MiniInfoCell before={<Icon24Linked height={20} width={20}/>}><Link href="https://zoomagasin.ru/" target="_blank">zoomagasin.ru</Link></MiniInfoCell>
                <Footer/>
            </Group>
        </Panel>
    )
};

export default Category;
